import {Component, Element, ComponentInterface, h, Host, JSX, Prop, State, Event, forceUpdate, EventEmitter} from '@stencil/core';
import {isDefined, isNotEmpty} from '../../utils/utils';
import {Keymap} from '../../utils/Keymap';

@Component({
  tag: 'soul-input-wrapper',
  styleUrl: 'soul-input-wrapper.scss',
  shadow: true
})
export class SoulInputWrapper implements ComponentInterface {

  @Prop({reflect: true}) public additionalLabel: string;
  @Prop({reflect: true}) public clearButton: boolean = false;
  @Prop({reflect: true}) public inputOnly: boolean = false;
  @Prop({reflect: true}) public helpText: string;
  @Prop({reflect: true}) public invalidStyle: boolean = false;
  @Prop({reflect: true}) public label: string;
  @Prop({reflect: true}) public status: 'warning' | 'critical';
  @Prop({reflect: true}) public statusMessage: string;
  @Prop({reflect: true}) public tooltipPosition: 'top' | 'top-start' | 'top-end' | 'right' | 'right-start' | 'right-end' | 'bottom' | 'bottom-start' | 'bottom-end' | 'left' | 'left-start' | 'left-end' = 'top-start';

  @Event() public soulClear: EventEmitter<void>;

  @State() private showClearButton = false;
  @State() private isPasswordRevealed = false;
  @State() private invalid = false;
  @Element() private hostElement: HTMLElement;

  private readonly instanceId = `soul-input-wrapper-${++inputWrapperId}`;
  private readonly INPUT_TYPE_ATTRIBUTE = 'type';
  private readonly INPUT_TYPE_DATA_ATTRIBUTE = 'data-type';
  private inputSlot: HTMLSlotElement;
  private inputElement: HTMLInputElement;
  private overlayElement: HTMLDivElement;
  private typeObserver: MutationObserver;
  private tooltipElement: HTMLSoulTooltipElement;
  private overlayResizeObserver = new ResizeObserver(this.setInputRightPadding.bind(this));
  private supportedStatuses = ['warning', 'critical'];
  private readonly dateTimeTypes = ['date', 'month', 'week', 'time', 'datetime-local'];
  private readonly typesWithClearButton = ['text', 'email', 'tel', 'url', 'search', 'color'];

  disconnectedCallback(): void {
    this.typeObserver?.disconnect();
  }

  componentDidRender(): void {
    this.setTooltipTriggerElement();
  }

  public render(): JSX.Element {
    return (
      <Host aria-label={this.label}>
        <div
          id={this.instanceId}
          class={{
            'soul-input-wrapper': true,
            'soul-input-wrapper--invalid': (this.invalidStyle && this.invalid),
            'soul-input-wrapper--warning': this.status === 'warning',
            'soul-input-wrapper--critical': this.status === 'critical',
            'soul-input-wrapper--readonly': this.isReadonly
          }}>
          {this.renderLabel()}
          <div class="soul-input-wrapper__control">
            {this.renderTooltip()}
            {this.renderSearchIcon()}
            <slot onSlotchange={this.onInputSlotChange.bind(this)} ref={el => this.inputSlot = el as HTMLSlotElement}></slot>
            {this.renderInputOverlays()}
          </div>
          {this.renderStatusMessage()}
          {this.renderHelpText()}
          {this.renderAction()}
        </div>
      </Host>
    );
  }

  private initializeInput(): void {
    this.inputElement = this.inputSlot.assignedElements()[0] as HTMLInputElement;
    this.inputElement.addEventListener('input', this.onInput.bind(this));
    this.inputElement.addEventListener('focus', this.setClearButtonVisibility.bind(this));
    this.inputElement.addEventListener('blur', this.setClearButtonVisibility.bind(this));
    this.inputElement.addEventListener('mouseleave', this.setClearButtonVisibility.bind(this));
    this.inputElement.addEventListener('mouseenter', this.setClearButtonVisibility.bind(this));
    if (this.inputType === 'search') {
      this.inputElement.addEventListener('keydown', this.onKeyDown.bind(this));
    }
    this.overlayResizeObserver.disconnect();
    this.overlayResizeObserver.observe(this.overlayElement);
    this.checkInputValidity();
    this.listenForTypeChange();
    this.setTooltipTriggerElement();
  }

  private get isReadonly(): boolean {
    return this.inputElement?.readOnly;
  }

  private get isPasswordType(): boolean {
    return this.inputType === 'password' || this.inputElement?.getAttribute('data-type') === 'password';
  }

  private get isDateType(): boolean {
    return this.dateTimeTypes.includes(this.inputType);
  }

  private get isSearchType(): boolean {
    return this.inputType === 'search';
  }

  private get isStatusValid(): boolean {
    return this.supportedStatuses.includes(this.status);
  }

  private get inputType(): string {
    return this.inputElement?.type;
  }

  public get shouldRenderStatusMessage(): boolean {
    return !this.isReadonly && ((this.isStatusValid || (this.invalidStyle && this.invalid)) && isNotEmpty(this.statusMessage));
  }

  public get shouldRenderCustomIcon(): boolean {
    return this.isDateType && this.showCustomIconForCurrenBrowser;
  }

  public get showCustomIconForCurrenBrowser(): boolean {
    return !navigator.userAgent.includes('Firefox');
  }

  public get shouldRenderTooltip(): boolean {
    return this.inputOnly && (this.shouldRenderStatusMessage || isNotEmpty(this.helpText));
  }

  public get shouldRenderStatusIndicator(): boolean {
    return !this.isReadonly && (this.isStatusValid || (this.invalidStyle && this.invalid));
  }

  public get shouldRenderClearButton(): boolean {
    return !this.isReadonly && this.isClearSupported && this.showClearButton && this.clearButton;
  }

  public get isClearSupported(): boolean {
    return this.typesWithClearButton.includes(this.inputType) && !this.isPasswordType;
  }

  private setTooltipTriggerElement(): void {
    if (isDefined(this.tooltipElement)) {
      this.tooltipElement.triggerElement = this.inputElement;
    }
  }

  private setInputRightPadding(): void {
    this.hostElement.style.setProperty('--soul-input-wrapper-input-padding-right', this.getOverlayElementWidth());
  }

  private getOverlayElementWidth(): string {
    return this.overlayElement ? `${this.overlayElement.offsetWidth}px` : '0px';
  }

  private onInputSlotChange(): void {
    if (this.inputElement !== this.inputSlot.assignedElements()[0]) {
      this.initializeInput();
      forceUpdate(this);
    }
  }

  private checkInputValidity(): void {
    this.invalid = !this.inputElement.checkValidity();
  }

  private focusInput(): void {
    this.inputElement.focus();
  }

  private onInput(): void {
    this.showClearButton = this.inputElement.value.length > 0;
    this.checkInputValidity();
  }

  private onKeyDown(event: KeyboardEvent): void {
    if (event.key === Keymap.ESC) {
      event.preventDefault();
      this.onClearButtonClick();
    }
  }

  private setClearButtonVisibility(): void {
    this.showClearButton = this.inputElement.matches(':focus') || this.inputElement.matches(':hover') || this.hostElement.matches(':focus-within') || this.overlayElement?.matches(':hover') ? this.inputElement.value.length > 0 : false;
  }

  private onClearButtonClick(): void {
    this.inputElement.value = '';
    this.showClearButton = false;
    this.checkInputValidity();
    this.focusInput();
    this.soulClear.emit();
  }

  private onDateTimeIconClick(): void {
    this.inputElement.focus();
    this.inputElement.showPicker();
  }

  private onPasswordVisibilityToggle(): void {
    if (this.inputElement.type === 'password') {
      this.inputElement.setAttribute(this.INPUT_TYPE_DATA_ATTRIBUTE, 'password');
      this.inputElement.setAttribute(this.INPUT_TYPE_ATTRIBUTE, 'text');
      this.isPasswordRevealed = true;
    } else if (this.inputElement.getAttribute(this.INPUT_TYPE_DATA_ATTRIBUTE) === 'password') {
      this.inputElement.setAttribute(this.INPUT_TYPE_ATTRIBUTE, 'password');
      this.inputElement.removeAttribute(this.INPUT_TYPE_DATA_ATTRIBUTE);
      this.isPasswordRevealed = false;
    }
  }

  private hasSlottedAction(): boolean {
    return !!this.hostElement.querySelector('[slot="action"]');
  }

  private listenForTypeChange(): void {
    this.typeObserver = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        if(mutation.attributeName === 'type' && mutation.oldValue === 'text' && this.inputElement.type !== 'password') {
          this.inputElement.removeAttribute(this.INPUT_TYPE_DATA_ATTRIBUTE);
        }
      });
      this.isPasswordRevealed = this.inputElement.type !== 'password';
      this.checkInputValidity();
      forceUpdate(this);
    });
    this.typeObserver.observe(this.inputElement, {attributes: true, attributeOldValue : true});
  }

  private renderLabel(): JSX.Element {
    return (this.label || this.additionalLabel) ?
      <div class="soul-input-wrapper__label">
        <label onClick={this.focusInput.bind(this)}>{this.label}{this.renderAdditionalLabel()}</label>
      </div> : '';
  }

  private renderAdditionalLabel(): JSX.Element {
    return this.additionalLabel ?
      <span class="soul-input-wrapper__additional-label">{this.additionalLabel}</span> : '';
  }

  private renderTooltip(): JSX.Element {
    return this.shouldRenderTooltip ?
      <soul-tooltip text={this.shouldRenderStatusMessage ? this.statusMessage : this.helpText}
                    status={this.status ? this.status : (this.invalid && this.invalidStyle) ? 'critical' : undefined} position={this.tooltipPosition}
                    ref={el => this.tooltipElement = el as HTMLSoulTooltipElement}></soul-tooltip> : '';
  }

  private renderSearchIcon(): JSX.Element {
    return this.isSearchType ? <soul-icon name="search" size="s" class="soul-input-wrapper__search-icon"></soul-icon> : '';
  }

  private renderInputOverlays(): JSX.Element {
    return <div class={{
      'soul-input-wrapper__input-overlays': true,
      'soul-input-wrapper__input-overlays--compact': !this.showCustomIconForCurrenBrowser && this.shouldRenderStatusIndicator && this.inputType?.includes('date')
    }} data-type={this.inputType} ref={el => this.overlayElement = el as HTMLDivElement}>
      {this.renderClearButton()}
      {this.renderPasswordVisibilityToggle()}
      {this.renderCalendarIcon()}
      {this.renderStatusIndicator()}
    </div>;
  }

  private renderPasswordVisibilityToggle(): JSX.Element {
    return !this.isReadonly && this.isPasswordType ?
      <button class="soul-input-wrapper__password-toggle soul-button soul-button--ghost soul-button--no-space soul-button--icon-only"
              onClick={this.onPasswordVisibilityToggle.bind(this)}>
        <div class="soul-button__icon">
          <soul-icon name={this.isPasswordRevealed ? 'hide' : 'show'} size="s"></soul-icon>
        </div>
      </button> : '';
  }

  private renderStatusIndicator(): JSX.Element {
    return this.shouldRenderStatusIndicator ?
      <soul-indicator size="s" type={this.status === 'warning' && !this.invalid ? 'warning' : 'critical'}></soul-indicator> : '';
  }

  private renderCalendarIcon(): JSX.Element {
    return !this.isReadonly && this.shouldRenderCustomIcon ?
      <button class=" soul-button soul-button--ghost soul-button--no-space soul-button--icon-only" onClick={this.onDateTimeIconClick.bind(this)}>
        <div class="soul-button__icon">
          <soul-icon size="m" name={this.inputType === 'time' ? 'time' : 'calendar'}></soul-icon>
        </div>
      </button>
      : '';
  }

  private renderClearButton(): JSX.Element {
    return this.shouldRenderClearButton ?
      <button class="soul-input-wrapper__clear soul-button soul-button--ghost soul-button--no-space soul-button--icon-only" onClick={this.onClearButtonClick.bind(this)}
              onBlur={this.setClearButtonVisibility.bind(this)}>
        <div class="soul-button__icon">
          <soul-icon name="close" size="s" color="reduced"></soul-icon>
        </div>
      </button> : '';
  }

  private renderStatusMessage(): JSX.Element {
    return this.shouldRenderStatusMessage ?
      <div class="soul-input-wrapper__status-message">{this.statusMessage}</div> : '';
  }

  private renderHelpText(): JSX.Element {
    return !this.isReadonly && this.helpText ?
      <div class="soul-input-wrapper__help">{this.helpText}</div> : '';
  }

  private renderAction(): JSX.Element {
    return !this.isReadonly && this.hasSlottedAction() ?
      <div class="soul-input-wrapper__action">
        <slot name="action"></slot>
      </div> : '';
  }

}

let inputWrapperId = 0;
