@use "modules/base-styles/reset";
@use "modules/core";
@use "modules/core/mixins/input";
@use "modules/components/label";
@use "modules/components/button";

:host {
  display: block;
  --soul-input-wrapper-input-width: 1fr;
  --soul-input-wrapper-input-padding-right: #{core.$soul-space-unit-xs};
}

:host([input-only]:not([input-only="false"])) {
  .soul-input-wrapper__label,
  .soul-input-wrapper__status-message,
  .soul-input-wrapper__help,
  .soul-input-wrapper__action {
    display: none;
  }
}

.soul-input-wrapper {
  display: grid;
  grid-template-columns: var(--soul-input-wrapper-input-width) auto;
  grid-template-rows: auto auto auto auto;
  grid-template-areas: "label label" "input action" "status status" "help help";

  &:not(.soul-input-wrapper--readonly) {
    .soul-input-wrapper__label {
      @include label.label-input();
    }
  }

  &.soul-input-wrapper--readonly {
    .soul-input-wrapper__label {
      @include label.label-key();
    }
  }
}

.soul-input-wrapper__label {
  grid-area: label;
  @include label.label();
}

.soul-input-wrapper__search-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: core.$soul-space-unit-xs + core.$soul-space-unit-3-xs;
  --soul-icon-color: #{core.$soul-color-background-icon};
}

.soul-input-wrapper__control {
  grid-area: input;
  position: relative;
}

.soul-input-wrapper__input-overlays {
  position: absolute;
  display: flex;
  column-gap: core.$soul-space-2-xs;
  top: 50%;
  transform: translateY(-50%);
  right: core.$soul-space-xs;
  padding: core.$soul-space-inline-left-s;

  &[data-type="date"],
  &[data-type="time"],
  &[data-type="week"],
  &[data-type="month"],
  &[data-type="datetime-local"] {
    background-color: core.$soul-color-background-primary;
  }
}

.soul-input-wrapper__input-overlays--compact {
  padding: 0;
}

.soul-input-wrapper__action {
  grid-area: action;
  padding: core.$soul-space-inline-left-2-xs;
}

.soul-input-wrapper__status-message {
  grid-area: status;
}

.soul-input-wrapper__help {
  grid-area: help;
}

.soul-input-wrapper__additional-label,
.soul-input-wrapper__help {
  @include label.label();
  @include core.soul-font-weight-regular();
  color: core.$soul-color-text-reduced-on-light;
}

.soul-input-wrapper__additional-label {
  padding: core.$soul-space-inline-left-2-xs;
}

.soul-input-wrapper__status-message,
.soul-input-wrapper__help {
  padding: core.$soul-space-stack-top-2-xs;
}

::slotted(input) {
  @include input.input-element-base();
  text-overflow: ellipsis;
  max-height: calc(#{input.$line-height-input-text} + #{2 * input.$padding-input-text} + #{2 * input.$border-with-input-text});
}

::slotted(input[type="color"]) {
  height: core.$soul-space-unit-xl + core.$soul-space-unit-3-xs;
  padding: core.$soul-space-squish-xs;
}

::slotted(input[type="text"]),
::slotted(input[type="password"]),
::slotted(input[type="number"]),
::slotted(input[type="url"]),
::slotted(input[type="email"]),
::slotted(input[type="search"]),
::slotted(input[type="color"]) {
  padding-right: var(--soul-input-wrapper-input-padding-right);
}

::slotted(input[type="search"]) {
  padding-left: core.$soul-space-unit-xl;
}

@media (hover: hover) and (pointer: fine) {
  ::slotted(input:hover:not(:disabled)) {
    box-shadow: core.$soul-shadow-hover-default-inner;
  }

  .soul-input-wrapper--critical {
    ::slotted(input:hover:not(:disabled):not([readonly])) {
      box-shadow: core.$soul-shadow-hover-negative-inner;
    }
  }

  .soul-input-wrapper--invalid {
    ::slotted(input:invalid:hover:not(:disabled):not([readonly])) {
      box-shadow: core.$soul-shadow-hover-negative-inner;
    }
  }

  .soul-input-wrapper--warning {
    ::slotted(input:hover:not(:disabled):not([readonly])) {
      box-shadow: core.$soul-shadow-hover-warning-inner;
    }
  }

  ::slotted(input[readonly]:hover) {
    box-shadow: none;
  }
}

::slotted(input:focus:not(:disabled)) {
  border-color: core.$soul-color-border-input-focus;
  box-shadow: core.$soul-shadow-focus-default-inner;
}

.soul-input-wrapper--warning {
  .soul-input-wrapper__status-message {
    color: core.$soul-color-text-indicator-warning;
  }

  ::slotted(input:not([readonly])) {
    border-color: core.$soul-color-border-warning;
  }

  ::slotted(input:focus:not(:disabled):not([readonly])) {
    border-color: core.$soul-color-border-warning;
    box-shadow: core.$soul-shadow-focus-warning-inner;
  }
}

.soul-input-wrapper--invalid,
.soul-input-wrapper--critical {
  .soul-input-wrapper__status-message {
    color: core.$soul-color-text-indicator-negative;
  }

  ::slotted(input:not([readonly])) {
    border-color: core.$soul-color-border-negative;
  }

  ::slotted(input:focus:not(:disabled):not([readonly])),
  ::slotted(input:invalid:focus:not(:disabled):not([readonly])) {
    border-color: core.$soul-color-border-negative;
    box-shadow: core.$soul-shadow-focus-negative-inner;
  }
}

::slotted(input:disabled) {
  background-color: core.$soul-color-background-disabled;
  color: core.$soul-color-text-disabled;
  cursor: not-allowed;
}

::slotted(input[readonly]) {
  display: inline-flex;
  background-color: core.$soul-choice-color-transparent;
  padding: 0;
  width: auto;

  border-top-style: none;
  border-bottom-style: none;
  border-left-style: none;
  border-right-style: none;
}

::slotted(input[readonly]:focus) {
  box-shadow: core.$soul-shadow-hover-default-inner;
}

::slotted(input[readonly]:placeholder-shown) {
  display: none;
}

::slotted(input[readonly]::placeholder) {
  color: core.$soul-choice-color-transparent;
}

// Rule to manage the date and datetime-local UI helper in Firefox
@supports (-moz-appearance:none) {
  .soul-input-wrapper--critical,
  .soul-input-wrapper--warning,
  .soul-input-wrapper--invalid {
    ::slotted(input[type*="date"]:not([readonly])) {
      padding-right: core.$soul-space-l;
    }
  }
}
